







































































































































































































import Vue from "vue";
import Chart from "./Chart.vue";
// import Percents from "./Percents.vue";
import * as util from "@/util";

enum Text {
  DEATHS = "Deaths",
  DEATHS_PERCENT = "Deaths %",
  RECOVERED_PERCENT = "Recovered %",
  CRITICAL_PERCENT = "Critical %",
  SERIOUS_PERCENT = "Serious %",
  RECOVERED = "Recovered",
  CRITICAL = "Critical",
  SERIOUS = "Serious",
  CASES = "Cases"
}

export default Vue.extend({
  name: "HelloWorld",
  props: {
    msg: String
  },
  components: {
    Chart
    //  Percents
  },
  data() {
    return {
      text: Text,
      PLACE_NAME: "OTHER PLACES",
      showCriticalLine: true,
      showCasesLine: true,
      showSeriousLine: true,
      showRecoveredLine: true,
      showDeathsLine: true,
      combine: true,
      checkedRows: []
    };
  },
  computed: {
    allData() {
      return util.getSuper();
    },
    total() {
      return util.getTotal();
    },
    checkedNames() {
      const arr = this.checkedRows.map(obj => {
        return obj[this.PLACE_NAME];
      });

      return arr;
    }
  },
  methods: {
    selectEurope() {
      //this.checkedNames = util.getEuropeNames();
      this.checkedRows = util.getEurope();
    },
    selectAll() {
      this.checkedNames = util.getAllNames();
    },
    clearAll() {
      this.checkedNames = [];
    },
    selectNA() {
      //this.checkedNames = util.getNorthAmericaNames();
      this.checkedRows = util.getNorthAmerica();
    },
    selectEuropeNA() {
      // this.checkedNames = util.getEuropeAndNorthAmericaNames();
      this.checkedRows = util.getEuropeAndNorthAmerica();
    },
    selectNordic() {
      //this.checkedNames = util.getNordicNames();
      this.checkedRows = util.getNordic();
    },
    getSelectedLatest() {
      return util.getSelectedLatest(this.checkedNames);
    },
    getLatest() {
      return this.allData[this.allData.length - 1];
    },
    getRecovered(str: string): any {
      return util.getRecovered(str);
    },

    getCritical(str: string): any {
      return util.getCritical(str);
    },
    getSerious(str: string): any {
      return util.getSerious(str);
    },

    nameClick(name: string) {
      this.checkedNames = [name];
    },
    selectedData() {
      return util.getPlace(this.checkedNames);
    },
    chartDeathsLine() {
      if (this.showDeathsLine) {
        return this.selectedData().deaths;
      }
      return undefined;
    },
    chartCasesLine() {
      if (this.showCasesLine) {
        return this.selectedData().cases;
      }
      return undefined;
    },
    chartRecoveredLine() {
      if (this.showRecoveredLine) {
        return this.selectedData().recovered;
      }
      return undefined;
    },
    chartSeriousLine() {
      if (this.showSeriousLine) {
        return this.selectedData().serious;
      }
      return undefined;
    },
    chartCriticalLine() {
      if (this.showCriticalLine) {
        return this.selectedData().critical;
      }
      return undefined;
    },
    chartDates() {
      return this.selectedData().dates;
    }
  },
  created() {
    this.selectEurope();
  }
});
