import jsonFile from "@/data.json";

export const jsonData = jsonFile;

/*
{
    "date": "2020-03-xx",
    "exactDate": "xxx",
    "mainlandChina":
    ,
    "otherPlaces":
    
},
*/

const PLACE_KEY = "OTHER PLACES";


export function parseNumber(value: string) {

  if ((typeof value === "number")) {
    return value;
  }

  let temp = value
    .replace(",", "")
    .replace("-", "")
    .trim();
  temp = temp.replace("*", "");


  if (!temp || temp === '') {
    return 0;
  }
  const loop = parseInt(temp);


  return loop;
}



function createPercentVal(total, share): number {

  const totaali = parseNumber(total);
  const shari = parseNumber(share);

  return parseFloat(((shari / totaali) * 100).toFixed(2));
}

function createPercentValM(total, share): number {


  const totaali = parseNumber(total);
  const shari = parseNumber(share);


  return parseFloat(((shari / totaali) * 100).toFixed(2));
}

function parseHelper(str: string, search: string) {
  // Parses a value from notes field

  if (!str || str.length === 0) {
    return 0;
  }

  const arr = str.split(",");

  let ret = 0;

  arr.forEach(element => {
    if (element.toLowerCase().includes(search.toLowerCase())) {
      ret = parseInt(element);
    }
  });

  return ret;
}

export function getRecovered(str: string): any {
  return parseHelper(str, "recovered");
}

export function getCritical(str: string): any {
  return parseHelper(str, "critical");
}
export function getSerious(str: string): any {
  return parseHelper(str, "serious");
}


export function getSuper() {
  let uusi: any[] = [];

  jsonData.forEach((element: any) => {
    element.otherPlaces.forEach((place: any) => {
      place.name = place[PLACE_KEY];
      place.cases = parseNumber(place.Cases);
      place.deaths = parseNumber(place.Deaths);

      if (place.Critical) {
        place.critical = parseNumber(place.Critical);
      } else {
        place.critical = getCritical(place.Notes);
      }

      if (place.Recovered) {
        place.recovered = parseNumber(place.Recovered);
      } else {
        place.recovered = getRecovered(place.Notes);
      }

      if (place.Serious) {
        place.serious = parseNumber(place.Serious);
      } else {
        place.serious = getSerious(place.Notes);
      }

      place.date = element.date;
      place.exactDate = element.exactDate;

      place.deathsPercent = createPercentVal(place.cases, place.deaths);
      place.recoveredPercent = createPercentVal(place.cases, place.recovered);
      place.seriousPercent = createPercentVal(place.cases, place.serious);
      place.criticalPercent = createPercentVal(place.cases, place.critical);
      const combined = place.critical + place.serious;
      place.combined = combined;
      place.combinedPercent = createPercentVal(place.cases, combined);
    });
    uusi.push(element);
  });

  uusi = uusi.sort((a: any, b: any) => {
    if (a.date < b.date) {
      return -1;
    }
    if (a.date > b.date) {
      return 1;
    }
    return 0;
  });

  return uusi;
}

function mergeValues(target, source) {
  let ret = 0;

  if (target) {
    ret = target;
  }

  if (source) {
    ret = ret + source;
  }

  return ret;
}

// Return data for the linechart
export function getPlace(placeName: string[]) {
  if (!placeName || placeName.length === 0) {
    return {};
  }

  const cases: number[] = [];
  const deaths: number[] = [];
  const recovered: number[] = [];
  const critical: number[] = [];
  const serious: number[] = [];
  const dates: string[] = [];
  const superior = getSuper();

  const deathsPercent: number[] = [];
  const recoveredPercent: number[] = [];
  const criticalPercent: number[] = [];
  const seriousPercent: number[] = [];
  const combinedPercent: number[] = [];

  superior.forEach((element, index) => {
    cases[index] = 0;
    critical[index] = 0;
    recovered[index] = 0;
    serious[index] = 0;
    deaths[index] = 0;
    dates.push(element.date);

    element.otherPlaces.forEach(place => {
      if (placeName.includes(place.name)) {
        place.date = element.date;
        place.exactDate = element.exactDate;
        cases[index] = mergeValues(cases[index], place.cases);
        critical[index] = mergeValues(critical[index], place.critical);
        recovered[index] = mergeValues(recovered[index], place.recovered);
        serious[index] = mergeValues(serious[index], place.serious);
        deaths[index] = mergeValues(deaths[index], place.deaths);
        deathsPercent[index] = createPercentVal(cases[index], deaths[index]);
        recoveredPercent[index] = createPercentVal(
          cases[index],
          recovered[index]
        );
        seriousPercent[index] = createPercentVal(cases[index], serious[index]);
        criticalPercent[index] = createPercentVal(
          cases[index],
          critical[index]
        );

        const combined = critical[index] + serious[index];
        combinedPercent[index] = createPercentVal(cases[index], combined);
      }
    });
  });

  return {
    cases,
    deaths,
    critical,
    recovered,
    serious,
    dates,
    deathsPercent,
    recoveredPercent,
    seriousPercent,
    criticalPercent,
    combinedPercent
  };
}

export function getTotal() {
  const superior = getSuper();

  let totalCases = 0;
  let totalRecovered = 0;
  let totalCritical = 0;
  let totalSerious = 0;
  let totalDead = 0;

  superior[superior.length - 1].otherPlaces.forEach(place => {
    place.name = place[PLACE_KEY];
    // place.cases = parseNumber(place.Cases);
    totalCases += place.cases;
    totalRecovered += place.recovered;
    totalCritical += place.critical;
    totalSerious += place.serious;
    totalDead += place.deaths;
  });

  const deathsPercent = createPercentValM(totalCases, totalDead);
  const recoveredPercent = createPercentValM(totalCases, totalRecovered);
  const seriousPercent = createPercentValM(totalCases, totalSerious);
  const criticalPercent = createPercentValM(totalCases, totalCritical);
  const combined = totalCritical + totalSerious;
  const combinedPercent = createPercentValM(totalCases, combined);

  return {
    cases: totalCases,
    recovered: totalRecovered,
    serious: totalSerious,
    deaths: totalDead,
    critical: totalCritical,
    deathsPercent,
    recoveredPercent,
    seriousPercent,
    criticalPercent,
    combined,
    combinedPercent
  };
}

export function getAllNames() {
  const superior = getSuper();
  const names: string[] = [];

  superior.forEach(element => {
    element.otherPlaces.forEach((place: any) => {
      if (!names.includes(place.name)) {
        names.push(place.name);
      }
    });
  });
  return names;
}

