export function getPercents(placeName: string[]) {
  if (!placeName || placeName.length === 0) {
    return {};
  }

  const superior = getSuper();

  let totalCases = 0;
  let totalRecovered = 0;
  let totalCritical = 0;
  let totalSerious = 0;
  let totalDead = 0;

  superior[superior.length - 1].otherPlaces.forEach(place => {
    if (placeName.includes(place.name)) {
      place.name = place[PLACE_KEY];
      place.cases = parseInt(place.Cases);
      totalCases += place.cases;
      totalRecovered += place.recovered;
      totalCritical += place.critical;
      totalSerious += place.serious;
      totalDead += place.deaths;
    }
  });

  const deathsPercent = createPercentVal(totalCases, totalDead);
  const recoveredPercent = createPercentVal(totalCases, totalRecovered);
  const seriousPercent = createPercentVal(totalCases, totalSerious);
  const criticalPercent = createPercentVal(totalCases, totalCritical);
  const combined = totalCritical + totalSerious;
  const combinedPercent = createPercentVal(totalCases, combined);

  return {
    deathsPercent,
    recoveredPercent,
    seriousPercent,
    criticalPercent,
    combinedPercent
  };
}

export function getByNames(names: any) {

  const superior = getSuper();

  const arr = [];

  superior[superior.length - 1].otherPlaces.forEach(place => {
    if (names.includes(place.name)) {
      arr.push(place);
    }
  });

  return arr;
}

export function getNordic() {
  return getByNames(NORDIC_NAMES);
}

export function getEurope() {
  return getByNames(EUROPEAN_NAMES);
}


export function getNorthAmerica() {
  return getByNames(NORTH_AMERICAN_NAMES);
}

export function getEuropeAndNorthAmerica() {
  return getByNames(EUROPEAN_NAMES.concat(NORTH_AMERICAN_NAMES));
}

export function getSelectedLatest(placeName: string[]) {
  if (!placeName || placeName.length === 0) {
    return {};
  }

  let cases = 0;
  let deaths = 0;
  let recovered = 0;
  let critical = 0;
  let serious = 0;
  let combined = 0;
  const superior = getSuper();

  let deathsPercent = 0;
  let recoveredPercent = 0;
  let criticalPercent = 0;
  let seriousPercent = 0;
  let combinedPercent = 0;

  superior[superior.length - 1].otherPlaces.forEach(place => {
    if (placeName.includes(place.name)) {
      cases = mergeValues(cases, place.cases);
      critical = mergeValues(critical, place.critical);
      recovered = mergeValues(recovered, place.recovered);
      serious = mergeValues(serious, place.serious);
      deaths = mergeValues(deaths, place.deaths);
      deathsPercent = createPercentVal(cases, deaths);
      recoveredPercent = createPercentVal(cases, recovered);
      seriousPercent = createPercentVal(cases, serious);
      criticalPercent = createPercentVal(cases, critical);
      combined = critical + serious;
      combinedPercent = createPercentVal(cases, combined);
    }
  });

  return {
    cases,
    deaths,
    critical,
    recovered,
    serious,
    deathsPercent,
    recoveredPercent,
    seriousPercent,
    criticalPercent,
    combined,
    combinedPercent
  };
}
