<script>
import { Line } from "vue-chartjs";
import _ from "lodash";

export default {
  extends: Line,
  props: [
    "deaths",
    "cases",
    "critical",
    "recovered",
    "serious",
    "dates",
    "combine"
  ],
  data() {
    return {
      options: {
        //responsive: false,
        maintainAspectRatio: false,

        title: {
          display: false
        },
        tooltips: {
          mode: "index",
          intersect: false
        },
        hover: {
          mode: "nearest",
          intersect: true
        },
        scales: {
          xAxes: [
            {
              display: true,
              scaleLabel: {
                display: true,
                labelString: "Day"
              }
            }
          ],
          yAxes: [
            {
              display: true,

              ticks: {
                beginAtZero: true,
                min: 0,
                stepSize: 1
              },

              scaleLabel: {
                display: true,
                labelString: "Value"
              }
            }
          ]
        }
      }
    };
  },

  computed: {
    getSeriousCritical() {
      if (!this.serious && !this.critical) {
        return [];
      }
      const retArr = [];
      for (let index = 0; index < this.critical.length; index++) {
        const element = this.critical[index] + this.serious[index];
        retArr.push(element);
      }

      return retArr;
    }
  },
  mounted() {
    console.log(this.getData());
    // this.renderLineChart();
    this.renderChart(this.getData(), this.options);
  },
  watch: {
    cases: function() {
      this.renderLineChart();
    },
    deaths: function() {
      this.renderLineChart();
    },
    critical() {
      this.renderLineChart();
    },
    serious() {
      this.renderLineChart();
    },
    recovered() {
      this.renderLineChart();
    },
    combine() {
      this.renderLineChart();
    }
  },

  methods: {
    renderLineChart() {
      console.log("I wanna render!!");
      this.bounceRender();
    },
    bounceRender: _.debounce(function() {
      console.log("Render");
      this.$data._chart.update();
      this.renderChart(this.getData(), this.options);
    }, 200),
    getLabels() {
      return this.dates;
    },
    getDataSets() {
      const ret = [];

      const cases = {
        label: "Cases",
        fill: false,
        backgroundColor: "#808080",
        borderColor: "#808080",
        data: this.cases,
        lineTension: 0
      };

      const deaths = {
        label: "Deaths",
        backgroundColor: "#000000",
        borderColor: "#000000",
        data: this.deaths,
        fill: false,
        lineTension: 0
      };

      const criticalSerious = {
        label: "Critical+Serious",
        fill: false,
        backgroundColor: "#CD0000",
        borderColor: "#CD0000",
        data: this.getSeriousCritical,
        lineTension: 0
      };

      const critical = {
        label: "Critical",
        fill: false,
        backgroundColor: "red",
        borderColor: "red",
        data: this.critical,
        lineTension: 0
      };
      const serious = {
        label: "Serious",
        fill: false,
        backgroundColor: "#FF5733",
        borderColor: "#FF5733",
        data: this.serious,
        lineTension: 0
      };

      const recovered = {
        label: "Recovered",
        fill: false,
        backgroundColor: "yellow",
        borderColor: "yellow",
        data: this.recovered,
        lineTension: 0
      };

      ret.push(cases);
      ret.push(deaths);
      ret.push(recovered);

      if (this.combine) {
        ret.push(criticalSerious);
      } else {
        ret.push(serious);
        ret.push(critical);
      }

      return ret;
    },
    getData() {
      return {
        labels: this.getLabels(),
        datasets: this.getDataSets()
      };
    }
  }
};
</script>

